(function () {
  'use strict';

  angular
    .module('spaStore', [
    'ui.router',
    'ui.bootstrap',
    'ui.checkbox',
    'ngAnimate',
    'toastr',
    'spaStore.admin',
    'spaStore.users',
    'froala',
    'ngFileUpload'
  ])
    .constant({
      CONSTANTS: {
        BASE: 'http://localhost:3030/api/',
        USERS_ENDPOINT: 'users/',
        DEVELOPMENT: {
          DEVELOPMENT_BASE: 'http://localhost:3030/api/'
        },
        PRODUCT_ENDPOINT: 'products/'
      }
    })
    .config(function ($stateProvider, $urlRouterProvider, $locationProvider, $httpProvider) {
      $locationProvider.html5Mode({enabled: true, requireBase: false});
      // Redirect any unmatched url
      $urlRouterProvider.otherwise("/dashboard");

      $stateProvider
        .state('main', {
        templateUrl: 'app/main/views/main.html',
        controller: 'MainController',
        abstract: true,
        controllerAs: 'main'
      })
        .state('main.dashboard', {
          cache: false,
          templateUrl: 'app/dashboard/views/dashboard.html',
          url: '/dashboard',
          data: {
            pageTitle: 'Welcome'
          },
          controller: 'DashboardController',
          controllerAs: 'dashboard'
        })
        .state('main.dashboard.product', {
          templateUrl: 'app/product/views/product.html',
          url: '/product/:id',
          controller: 'ProductController',
          controllerAs: 'product'
        })
        .state('main.category', {
          templateUrl: 'app/dashboard/views/dashboard.html',
          url: '/category/:category/:subCategory/:subSubCategory',
          controller: 'DashboardController',
          controllerAs: 'dashboard'
        })
        .state('main.category-page', {
          templateUrl: 'app/dashboard/views/dashboard.html',
          url: '/category-page/:category/:subCategory/:subSubCategory?page{pageNumber}',
          controller: 'DashboardController',
          controllerAs: 'dashboard'
        })
        .state('main.login', {
          templateUrl: 'app/users/login/views/login.html',
          url: '/login',
          controller: 'LoginController',
          controllerAs: 'login',
          resolve: {
            auth: function (userInfoService, $timeout, $state) {
              var user = userInfoService.getUserInfo();
              if (user) {
                if (user.username && user.username != 'Гост') {

                  $timeout(function () {
                    $state.go('main.dashboard');
                  }, 10)
                }
              }
            }
          }
        })
      $httpProvider
        .interceptors
        .push('httpInteceptors');
    })
    .run(function ($rootScope, $state) {
      $rootScope.$state = $state;
    })

})();

(function () {
  'use strict';

  angular
    .module('spaStore')
    .controller('DashboardController', function ($q, $rootScope, userInfoService, dashboardService, productService, $state, $timeout) {
      var vm = this;

      //region Caroussel

      vm.carousselInterval = 3000;
      vm.carousselActive = 1;
      vm.noWrapSlides = false;
      vm.carousselImages = [
        {
          image: 'http://www.taiwah.com.sg/images/products/thumbnail/18_20130118_10460878.jpg',
          id: 0,
          text: 'Promo text'
        }, {
          image: 'http://www.smartbuy.ru/images/anesi_brand.jpg',
          id: 1,
          text: 'Promo text'
        }
      ];

      //endregion

      userInfoService.loggedUser();

      vm.getCategories = function () {
        dashboardService
          .getCategories()
          .then(function (res) {
            
            vm.mainCategories = res;
          }, function (err) {
            console.log(err)
          })
      }
      var currentState = $state.current.name;

      if (currentState == 'main.dashboard') {
        productService
          .getAllProducts()
          .then(function (res) {
            vm.products = res;
            
          }, function (err) {})
      } else if (currentState == 'main.category') {
        productService
          .getProductForCategory($state.params)
          .then(function (res) {
            
            vm.products = res;
          }, function (err) {})
        vm.goToNextPage = function () {
          $state.go('main.category-page', {
            category: $state.params.category,
            subCategory: $state.params.subCategory,
            subSubCategory: $state.params.subSubCategory,
            page: 2
          })
        }
      } else if (currentState == 'main.category-page') {
        productService
          .getProductForCategoryAndPage($state.params)
          .then(function (res) {
            
            vm.products = res;
          }, function (err) {})
        vm.goToNextPage = function () {
          var page = $state.params.page + 1;
          $state.go('main.category-page', {
            category: $state.params.category,
            subCategory: $state.params.subCategory,
            subSubCategory: $state.params.subSubCategory,
            page: page
          })
        }
      }

    });
})();

