(function () {
  'use strict';

  angular
    .module('spaStore')
    .factory('userInfoService', function (commonMethods,$location,$window,$rootScope) {
      var userInfoService = this,
        userInfo;

      userInfoService.userInfo = {
        clientIsAuthenticated: false,
        clientUsername: ''
      };

      function setSessionStoreageInUse() {
        var userInfoData = commonMethods.getObjectFromLocalStorage('userInfo');

        if (userInfoData != null) {
          userInfoService.storageInUse = 'localStorage';
        } else {
          userInfoService.storageInUse = 'sessionStorage';
        }
      }
      setSessionStoreageInUse();

      userInfoService.setUserInfo = function (userInfo) {
        if (userInfoService.storageInUse == 'localStorage') {
          commonMethods.setObjectInLocalStorage('userInfo', userInfo);
        } else {
          commonMethods.setObjectInSessionStorage('userInfo', userInfo);
        }
      };

      userInfoService.getUserInfo = function () {
        var userInfo = commonMethods.getObjectFromLocalStorage('userInfo');

        if (userInfo == null) {
          userInfo = commonMethods.getObjectFromSessionStorage('userInfo');
        }

        return userInfo;
      };

      // userInfoService.checkTokenExpiration = function () {
      //   var currentDate = new Date();
      //   currentDate.setSeconds(currentDate.getSeconds() + 10);
      //   currentDate = Date.parse(currentDate);
      //
      //   var tokenDate = Date.parse($window.localStorage.expires);
      //
      //   if (isNaN(tokenDate)) {
      //     return false;
      //   }
      //
      //   return currentDate > tokenDate;
      // };

      userInfoService.logout = function () {
        $window.localStorage.clear();
        $window.sessionStorage.clear();
        $location.path('/login');
      };


      userInfoService.loggedUser = function(){
        $rootScope.userInfo =  userInfoService.getUserInfo();
        if(!$rootScope.userInfo){
          $rootScope.userInfo = {};
          $rootScope.userInfo.username= 'Гост'
        }
      };

      return this;
    });

}());

(function () {
  'use strict';

  angular
    .module('spaStore.users')
    .factory('registerHelper',
      function () {
        var registerHelper = this;

        function validateEmail(email)
        {
          var re = /\S+@\S+\.\S+/;
          return re.test(email);
        }


        registerHelper.validateUserModel = function (userModel) {
          //Check disclaimer
          if (!userModel.disclaimer) {
            return 'Тикчето с общите условия е задължително.';
          }

          //Validate Password
          if (userModel.password == null || userModel.password.length <= 4) {
            return 'Паролата трябва да е с минумум 5 символа';
          }
          if (userModel.password != userModel.confirmPassword) {
            return 'Паролите не съвпадат';
          }

          //validate username
          if (userModel.username == null || userModel.username.length <= 4) {
            return 'Потребителското име трябва да е с минумум 5 символа';
          }

          //validate name
          if (userModel.name == null || userModel.name.length <= 4) {
            return 'Полето с име е задължително';
          }

          //validate Email
          if(userModel.email==null || !validateEmail(userModel.email)){
            return 'Email полето е невалидно или празно';
          }

          return true;
        };


        return registerHelper;
      });
}());

